<template>
	<view>
		<view class="index" v-if="isFormal == 2">
			<!-- 轮播开始 -->
			<!-- #ifdef MP-WEIXIN -->
			<swiperImg :banner="banner" :indicatorDots="false" :autoplay="true" :interval="3000" :duration="500" />
			<!-- #endif -->
			<!-- #ifdef H5 -->
			<swiperImg :banner="banner" :indicatorDots="false" :autoplay="true" :interval="3000" :duration="500" />
			<!-- #endif -->

			<!-- 轮播结束 -->
			<!-- 内容开始 -->
			<view class="index_content">
				<!-- 卡片开始 -->
				<view class="card_wrap">
					<view class="card_item">
						<navigator url="../selectCode/selectCode" class="card_icon">
							<image mode="widthFix" src="https://fucai.yiruit.com/UpFiles/shouye/shuangse.png"></image>
						</navigator>
						<view class="card_title">双色球</view>
						<view class="card_text">2元可中1000万</view>
					</view>
					<view class="card_item">

						<navigator url="../selectCode/selectCodeqicai" class="card_icon">
							<image mode="widthFix" src="https://fucai.yiruit.com/UpFiles/shouye/qile.png"></image>
						</navigator>
						<view class="card_title">七乐彩</view>
						<view class="card_text">单注奖金最高500万</view>
					</view>
					<view class="card_item">

						<navigator url="../selectCode/selectThreeD" class="card_icon">
							<image mode="widthFix" src="https://fucai.yiruit.com/UpFiles/shouye/fucai.png"></image>
						</navigator>
						<view class="card_title">福彩3D</view>
						<view class="card_text">固定奖，天天开</view>
					</view>
				</view>
				<!-- 卡片结束 -->
				<!-- 彩卷开始 -->
				<view class="caijuan_wrap">
					<view @click="handleGua" class="caijuan_item">
						<image src="https://fucai.yiruit.com/UpFiles/shouye/scraping.png" mode="aspectFit"></image>
						<view class="caijuan_text">刮刮乐</view>
					</view>
					<navigator url="../luckyWheel/luckyWheel" class="caijuan_item">
						<image src="https://fucai.yiruit.com/UpFiles/shouye/luck.png" mode="aspectFit"></image>
						<view class="caijuan_text">幸运抽奖</view>
					</navigator>
					<navigator url="./caijuan" class="caijuan_item">
						<image src="https://fucai.yiruit.com/UpFiles/shouye/ticket.png" mode="aspectFit"></image>
						<view class="caijuan_text">体验卷</view>
					</navigator>
				</view>
				<!-- 彩卷结束 -->
				<!-- <navigator url="../mine/tuijian" class="fenge_bg">
					<image src="https://fucai.yiruit.com/UpFiles/shouye/GO.png" mode="widthFix"></image>
				</navigator> -->
				<navigator open-type="navigate" url="../mine/jifen" class="fenge_bg">
					<image src="https://fucai.yiruit.com/UpFiles/shouye/GO.png" mode="widthFix"></image>
				</navigator>
				<!-- 开奖开始 -->
				<view class="kaijiang_wrap">
					<view class="kaijiang_header">
						<view class="kaijiang_title"><text>开奖情况</text></view>
					</view>
					<navigator open-type='navigate' url="../kaijiang/shuangseDetail" class="kaijiang_item" v-for="(item,index) in shuangse" :key="index">
						<view class="kaijiang_item_icon">
							<image src="https://fucai.yiruit.com/UpFiles/shouye/shuangseqiu.png" mode="widthFix"></image>
							<view class="icon_text">双色球</view>
						</view>
						<view class="kaijiang_item_content">
							<view class="content_text">第{{item.PeriodsNumber}}期<text class="blcak">{{item.PrizeTime}}</text></view>
							<view class="content_qiu">
								<text v-for="red in item.RedBall" :key="red">{{red}}</text>
								<text class="blue_num">{{item.BlueBall}}</text>
							</view>
						</view>
						<view class="qiu_btn">
							<image src="https://fucai.yiruit.com/UpFiles/shouye/more.png" mode="aspectFit"></image>
						</view>
					</navigator>
					<navigator open-type='navigate' url="../kaijiang/qilecaiDetail" class="kaijiang_item" v-for="(item,index) in qicai" :key="item.ID">
						<!--  -->
						<view class="kaijiang_item_icon">
							<image src="https://fucai.yiruit.com/UpFiles/shouye/qilecai.png" mode="widthFix"></image>
							<view class="icon_text">七乐彩</view>
						</view>
						<view class="kaijiang_item_content">
							<view class="content_text">第{{item.PeriodsNumber}}期<text class="blcak">{{item.PrizeTime}}</text></view>
							<view class="content_qiu">
								<text v-for="red in item.RedBall" :key="red">{{red}}</text>
								<text class="blue_num">{{item.BlueBall}}</text>
							</view>
						</view>
						<view class="qiu_btn">
							<image src="https://fucai.yiruit.com/UpFiles/shouye/more.png" mode="aspectFit"></image>
						</view>
					</navigator>
					<navigator open-type='navigate' url="../kaijiang/fucaiDetail" class="kaijiang_item" v-for="item in fucai" :key="item.ID">
						<view class="kaijiang_item_icon">
							<image src="https://fucai.yiruit.com/UpFiles/shouye/fucai3d.png" mode="widthFix"></image>
							<view class="icon_text">福彩3D</view>
						</view>
						<view class="kaijiang_item_content">
							<view class="content_text">第{{item.PeriodsNumber}}期<text class="blcak">{{item.PrizeTime}}</text></view>
							<view class="content_qiu">
								<text v-for="(red,index) in item.RedBall" :key="index">{{red}}</text>
							</view>
						</view>
						<view class="qiu_btn">
							<image src="https://fucai.yiruit.com/UpFiles/shouye/more.png" mode="aspectFit"></image>
						</view>
					</navigator>
				</view>
				<!-- 开奖结束 -->
			</view>
			<!-- 内容结束 -->
		</view>

		<!-- isFormal==1 -->
		<view class="index_specil" v-else-if="isFormal==1">
			<view class='index_specil_content'>
				<!-- 轮播图 -->
				<view class="index_specil_banner">
					<swiper-item :banner="banner"></swiper-item>
				</view>
				<!-- 资讯 -->
				<view class="news">

					<view class="news_main">
						<view class="news_item" v-for="(item,index) in news" :key="index" @tap="goNewsDetail(item)">
							<view class="news_item_title">
								<view class="news_item_title_name">
									{{item.Title}}
								</view>
								<view class="news_item_title_time">
									{{item.AddTime}}
								</view>
							</view>
							<view class="news_item_img">
								<image :src='item.image' mode="scaleToFill"></image>
							</view>
						</view>
					</view>

				</view>
			</view>
		</view>
	</view>
</template>
<script>
	var wx = require('jweixin');
	import SwiperItem from "../../components/swiper/SwiperItem.vue";
	import common from '../../common/common';
	import swiperImg from '@/components/hswiper/swiper.vue'
	var _self;
	export default { 
		components: {
			SwiperItem,
			swiperImg 
		},
		data() {
			return {
				num: 3,
				banner: [],
				shuangse: [],
				qicai: [],
				fucai: [],
				city: "",
				qqMap: '', // 测试腾讯位置服务
				swiperList: [], // H5 轮播图
				isFormal: 1,
				shuangseUrl: '',
				news: [],
			};
		},
		onLoad() {
			_self = this;
			// #ifdef H5
			
			console.log('location.href',location.href.split('#')[0]);
			common.request({
				path:'login/getcode',
				method:'GET',
				data:encodeURIComponent(location.href.split('#')[0]),
				success(res){
					console.log('ticket',res)
					let {nonceStr,signature,timestamp,urls,url} = res.data;
					wx.config({
						debug: true, //开启调试模式，调用的所有api的返回参数都会在客户端alert出来，			  //	在PC端log出来
						appId: 'wxeb23f51daa27076e',
						timestamp,// 生成签名的时间戳 返回
						nonceStr,  //  生成签名的随机字符串 返回
						signature, // 加密签名 后台生成 返回
						jsApiList:[		// 需要使用的js-sdk列表
						'chooseImage',
						'openLocation',
						'getLocation',
						]
					})
				},
				fail(err){
					console.log('bbbb',err);
				}
				
			})
			wx.ready(()=>{
			 function getCity() {
					return new Promise((resolve, reject) => {
						wx.getLocation({
							type: "gcj02",
							success(res1) {
								let {latitude,longitude} = res1;
								let jwObj = {
									latitude,
									longitude
								}
								resolve(jwObj)
								console.log('wx.getLocation',res1); 
								uni.setStorageSync("jingwei", {
									latitude,
									longitude
								});
							},
							fail: err => {
								reject(err);
							},
							complete: () => {
				
							}
						});
					})
				}
				getCity().then(res => {
					let {latitude,longitude} = res;
					console.log('jwObj',res) 
					common.request({
						path:'Order/getaddress',
						data:{
							latitude,
							longitude
						},
						success:(result)=>{
							console.log('result',result);
						}
					})
					// if (res != "湘潭市") {
					// 	console.log('不在');
					// 	wx.showToast({
					// 		title: '您不在服务区范围内',
					// 		icon: 'none',
					// 		duration: 3000,
					// 		mask: true,
					// 	})
					// }
				})
				
				
				
				
				
				
				// _self.getAdress() // 获取城市名
				_self.getIndex();
				_self.getKaijiang();
				_self.getFucai();
				_self.getQicai();
					
			})
			
			
			// #endif
			
			
			// 小程序专用
			// #ifdef MP-WEIXIN 
			_self.getAdress() // 获取城市名
			_self.hangdleOpid(function() { // 是否注册过
				// 轮播图
				_self.getIndex();
				// 双色球
				_self.getKaijiang();
				// 福彩3d
				_self.getFucai();
				// 七乐彩
				_self.getQicai();

			});
			_self.getNews();

			// #endif
		},
		async onShow() {
			let result = await _self.getVersion();
			_self.isFormal = result;
			if (_self.isFormal == 2) {
				uni.setNavigationBarTitle({
					title: '福彩到家-77派'
				})
				uni.setTabBarItem({
					index:1,
					text:'开奖'
				})
				uni.setTabBarItem({
					index:2,
					text:'福彩到家'
				})
			}
		},
		methods: {
			// 去往新闻详情页
			goNewsDetail(item) {
				uni.navigateTo({
					url: '../news/newsDetail?item=' + encodeURIComponent(JSON.stringify(item))
				})
			},
			// 获取资讯
			getNews() {
				common.request({
					path: 'Development/getnewlist',
					data: {
						type: '1',
						page: 1,
						rows: 10,
					},
					success: (res) => {
						res.data.rows.forEach(item => {
							item.image = common.PicURL + item.image;
						})
						_self.news = res.data.rows;
					},
				})
			},
			getVersion() {
				return new Promise((resolve, reject) => {
					common.request({
						path: 'login/getversion',
						method: 'GET',
						success: (res) => {
							if (res.info == '响应成功') {
								resolve(res.data.Version);
							} else {
								reject(res.info)
							}
						}
					})
				})
			},
			getAdress() { // 获取用户的经纬度 得到城市
			
				common.getCity(_self.city).then(res => {
					console.log(res) 
					if (res != "湘潭市") {
						console.log('不在');
						wx.showToast({
							title: '您不在服务区范围内',
							icon: 'none',
							duration: 1500,
							mask: true,
						})
					}
				})
			},
			// opid获取判断 是否是用户
			hangdleOpid(callback) {
				uni.login({
					provider: 'weixin',
					success(loginResult) {
						// 获取微信登录的code:授权码
						const code = loginResult.code;
						common.request({
							path: "login/selecttoken",
							method: "GET",
							data: code,
							success: (res) => { // 是否有openid 是否曾经注册过	
								if (res.data.ID) { //  如果 那就直接登录							 
									uni.setStorageSync("openid", res.data.openId);
									uni.setStorageSync("userinfo", res.data);
									common.setstate(res.data);
									return callback();
								} else { // 没有openid  那显示注册
									uni.setStorageSync("openid", res.data.openinfo.openid); //	未注册时获取的openid								
									uni.navigateTo({
										url: "/pages/login/userLogin"
									})
								}
							}
						})
					}
				})
			},
			// 获取轮播数据
			getIndex() {
				common.request({
					path: "Development/getbanner",
					// method:'POST',
					data: {
						rows: 3,
						page: 1,
						sidx: "",
						sord: "desc",
					},
					success: res => {
						res.data.rows.forEach((element, index) => {
							element.Images = common.PicURL + element.Images
						});
						_self.banner = res.data.rows

					}
				})
			},

			// 获取七彩乐开奖历史
			getQicai() {
				common.request({
					path: "Development/getpage",
					data: {
						rows: 1,
						page: 1,
						sidx: "PrizeTime",
						sord: "desc",
						caizhongid: "3"
					},
					success: res => {
						uni.setStorageSync('qilecai', res.data.qishu)
						res.data.rows.forEach(elem => {
							elem.RedBall = elem.RedBall.split(",")
						})
						_self.qicai = res.data.rows
					}
				})
			},
			// 获取福彩开奖历史
			getFucai() {
				common.request({
					path: "Development/getpage",
					// method:'POST',
					data: {
						rows: 1,
						page: 1,
						sidx: "PrizeTime",
						sord: "desc",
						caizhongid: "2"
					},
					success: res => {
						uni.setStorageSync('threed', res.data.qishu)
						res.data.rows.forEach(elem => {
							elem.RedBall = elem.RedBall.split(",")
						})
						_self.fucai = res.data.rows
					}
				})
			},
			// 获取双色球开奖历史
			getKaijiang() {
				common.request({
					path: "Development/getpage",
					// method:'POST',
					data: {
						rows: 1,
						page: 1,
						sidx: "PrizeTime",
						sord: "desc",
						caizhongid: "1"
					},
					success: res => {
						uni.setStorageSync('shuangseqiu', res.data.qishu)
						res.data.rows.forEach(elem => {
							if (elem.RedBall) {
								elem.RedBall = elem.RedBall.split(",")
							}
						})
						_self.shuangse = res.data.rows;
						
					}
				})
			},
			handleGua() { // 刮刮乐
				uni.setStorageSync('type', 'guaguale')
				uni.switchTab({
					url: "../getHome/getHome",
				})
			}
		}
	}
</script>

<style lang="scss" scoped>
	.index_content {
		width: 95%;
		margin: 10rpx auto 0;
	}

	.blcak {
		color: #aaa;
	}

	.card_wrap {
		display: flex;
		margin: 15rpx 0;
		text-align: center;

		.card_item {
			width: 33.33%;

			.card_icon {
				width: 120rpx;
				margin: 15rpx auto;

				image {
					border-radius: 50%;
				}
			}

			.card_title {
				font-size: 32rpx;
			}

			.card_text {
				font-size: 26rpx;
				color: #666;
			}
		}
	}

	.caijuan_wrap {
		display: flex;
		justify-content: space-between;
		margin-top: 30rpx;

		.caijuan_item {
			display: flex;
			justify-content: space-around;
			width: 31%;
			background-color: #eeeeee;
			height: 100rpx;
			align-items: center;

			image {
				width: 23%;
			}

			.caijuan_text {
				display: flex;
				align-items: center;
			}
		}
	}

	.fenge_bg {
		width: 90%;
		margin: 20rpx auto;
	}

	.kaijiang_wrap {
		margin-top: 30rpx;

		.kaijiang_header {
			display: flex;
			justify-content: space-between;
			padding-left: 15rpx;

			.kaijiang_title {
				border-left: 10rpx solid $color;
				font-size: 30rpx;

				text {
					margin-left: 15rpx;
				}
			}

			.kaijiang_btn {
				font-size: 26rpx;

				text {
					margin-left: 5rpx;
				}
			}
		}

		.kaijiang_item {
			display: flex;
			flex-direction: row;
			justify-content: space-between;
			align-items: center;
			margin-top: 20rpx;
			background-color: #eee;
			height: 160rpx;

			&:last-of-type {
				margin-bottom: 20rpx;
			}

			.kaijiang_item_icon {
				flex: 0.9;
				display: flex;
				flex-direction: column;
				align-items: center;
				margin-left: -10rpx;

				image {
					width: 80rpx;
				}

				.icon_text {}
			}

			.kaijiang_item_content {
				flex: 3;
				height: 100%;
				overflow: hidden;
				display: flex;
				flex-direction: column;
				justify-content: space-around;

				.content_text {
					font-size: 26rpx;

					text {
						margin-left: 8rpx;
					}
				}

				.content_qiu {
					display: flex;
					justify-content: flex-start;
					align-items: center;

					text {
						display: block;
						width: 55rpx;
						height: 55rpx;
						background-color: $color;
						text-align: center;
						line-height: 55rpx;
						border-radius: 50%;
						color: #fff;
						margin-left: 10rpx;
					}

					.blue_num {
						background-color: blue;
					}
				}

				.content_info {
					font-size: 24rpx;
					color: #ccc;
					margin-top: 15rpx;
				}
			}

			.qiu_btn {
				width: 60rpx;
				height: 100%;
				display: flex;
				flex-direction: row;
				align-items: center;
				justify-content: center;

				image {
					width: 20%;
					height: 100%;
					display: block;
				}
			}
		}
	}


	.index_specil {
		.index_specil_content {
			.index_specil_banner {}

			.news {
				background-color: #F5F5F5;

				.news_main {
					margin-top: 20rpx;
					padding: 0 40rpx;

					.news_item {
						display: flex;
						padding: 45rpx 42rpx 30rpx;
						border-bottom: 1rpx solid #E7E7E7;

						.news_item_title {
							width: 374rpx;
							margin-right: 46rpx;
							display: flex;
							flex-direction: column;
							justify-content: space-between;

							.news_item_title_name {
								text-overflow: ellipsis;
								display: -webkit-box;
								/* autoprefixer: off */
								// 不能省略
								-webkit-box-orient: vertical;
								-webkit-line-clamp: 3;
								word-break: break-all;
								overflow: hidden;
							}

							.news_item_title_time {
								font-size: 24rpx;
							}


						}

						.news_item_img {
							width: 242rpx;
							height: 174rpx;
							border-radius: 14rpx;
							overflow: hidden;

							image {
								width: 242rpx;
								height: 174rpx;
							}
						}
					}
				}
			}
		}
	}
</style>
